.App {
  text-align: center;
  background-color: #FFFFFF;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  font-family: 'Founders Grotesque Regular';
  background-color: #FFFFFF;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  margin: 42px 110px;
  height: 50px;
  line-height: 50px;
  font-weight: 400;
  font-size: 18px;
  width: 1220px;
  position: absolute;
  z-index: 100;
}

footer.App-header {
  position: relative;
  margin: 42px 110px 0 110px;
}

.footer-privacy {
  font-size: 14px;
  margin: 0 110px;
  display: flex;
  justify-content: space-between;
  line-height: 30px;
}

.footer-privacy div {
  display: flex;
  margin-right: 50px;
}

.footer-privacy div img {
  margin-left: 20px;
}

.copyright { 
  font-size: 14px;
  color: #A5A9BB;
  text-align: start;
  padding: 30px 110px 50px 110px;
 }

.App-link {
  color: #61dafb;
}

nav {
  display: flex;
  position: relative;
}

nav .item {
  margin-right: 50px;
  cursor: pointer;
}

nav .item:hover {
  color: #7c7a7a;
}

nav .item img {
  float: right;
  margin: 24px 0px 0px 10px;
}

nav .button, .button {
  background: #171414;
  border-radius: 10px;
  color: #FFFFFF;
  padding: 0px 30px;
  line-height: 46px;
  height: 50px;
  cursor: pointer;
}

nav .button:hover, .button:hover {
  background: #4b4848;
  color: #FFFFFF;
}

nav .button img, .button img {
  float: right;
  margin: 16px 0px 0px 10px;
}

nav .button.left img, .button.left img {
  float: left;
  margin: 14px 10px 0px 0px;
}

nav .item .submenu {
  display: none;
  position: absolute;
  width: 388px;
  height: 218px;
  background: #FFFFFF;
  box-shadow: 0px 4px 34px rgba(37, 35, 130, 0.1);
  border-radius: 10px;
  left: 0px;
  flex-direction: column;
  align-items: flex-start;
  text-align: start;
}

nav .item .submenu .submenuitem {
  color: #000000;
  margin: 10px 10px 0px 10px;
  padding: 10px;
  line-height: 40px;
  font-weight: bold;
  display: inline-flex;
  align-items: flex-start;
}

nav .item .submenu .submenuitem:hover {
  background-color: #f0f0f0;
}

nav .item .submenu .submenuitem img {
  margin: 0px 20px 0px 10px;
  float: left;
}

nav .item .submenu .submenuitem span {
  color: #7c7a7a;
  line-height: initial;
  display: block;
  font-size: 15px;
  padding-bottom: 10px;
}

nav .item.language img {
  margin: 15px 0px 0px 0px
}

.homepage {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.homepage .investors {
  margin: 100px 213px;
}

.homepage .einvoice {
  margin: 0;
}

.homepage .einvoice1 {
  margin: 100px 0 0 0;
}

.homepage .buttons {
  position: absolute;
  top: 355px;
  display: flex;
  align-items: center;
}

.homepage .buttons .button {
  margin-right: 20px;
}

.homepage .buttons span {
  margin-left: 10px;
  font-size: 11px;
  width: 80px;
}

.homepage .heading {
  position: absolute;
  top: 126px;
}

.homepage .heading h1 {
  font-family: 'Playfair Display', serif;
  font-style: normal;
  font-weight: 600;
  font-size: 76px;
  line-height: 100%;
  margin: 20px 0;
}

.footer-divider {
  margin: 30px 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
